import { useCallback, useReducer, useEffect } from 'react';
import { stringify } from 'qs';
import { useFlexgetStream } from 'core/api';
import { camelize } from 'utils/fetch';
export const useLogStream = (options) => {
    const queryString = stringify(options);
    const [messages, addMessage] = useReducer((state, message) => message !== 'clear' ? [message, ...state] : [], []);
    const [{ readyState, stream }, { connect: connectFn, disconnect }] = useFlexgetStream(`/server/log?${queryString}`);
    useEffect(() => {
        stream?.node('{message task}', (message) => addMessage(camelize(message)));
    }, [stream]);
    const clear = useCallback(() => addMessage('clear'), []);
    const connect = useCallback(() => {
        clear();
        connectFn();
    }, [clear, connectFn]);
    return [
        { messages, readyState },
        { connect, disconnect, clear },
    ];
};
